@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);

body{
  margin: 0;
  padding: 0;
  background-color: lightgray;
}

h1{
  margin: 0;
}
.App{
    color: #181616;
    margin: 0;
    padding:0;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1.5px;    
}

h4{
    margin: 5px;
    font-size: 1.5rem;
}

.App > h1{
    padding: 20px;
    font-size: 3.5rem;
    text-align: center;
    color: white;
    background-color: #4bccf7;
    border-bottom: 3px solid  #005ecb;
}

.container{
    margin: 0 auto;
    border-radius: 25px;
    padding: 50px;
    max-width: 900px;
   
}

.box{
    display: flex;
    justify-content:center;
}

.box > .amountBox:last-child{
    background-color: #fcaaaa;
    border: 2px solid red;
    padding: 20px 50px;
    border-radius: 25px;
}

.box > .amountBox:first-child{
    background-color: #87f86d;
    border : 2px solid green;
    padding: 20px 50px;
    border-radius: 25px;
}



@media screen and (max-width: 600px){
    .App > h1{
        font-size: 2.8rem;
    }
    
    .box{
        flex-direction: column;
    }
    .balance{
        font-size: unset;
    }
    .amountBox{
        margin: 10px !important;
        text-align: center;
        padding: 20px 0px !important;
    }
    .amountBox > h1{
        font-size: 1.2rem;
    }

    .transaction > h1 {
        margin: 0;
        font-size: 1.2rem;
        height:  60px;
    }

    .transaction > h1 > span{
        float : unset !important;
    }

    .transaction-heading{
        font-size: 1.5rem;
    }

    .list {
        margin: 0;
        flex-direction: column;
    }
    .list p{
        font-size: 1.3rem !important;
        width: unset !important;
    }
}

a{
    padding: unset !important;
    color: unset !important;
    background-color: unset !important;
    border: none !important;
}

.pdf{
    background-color: white;
    color: red;
    font-size: 22px;
    padding: 10px 15px;
    border: 1px solid red;
    border-radius: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.icon-pdf{
    font-size: 24px;
}

.disabled{
    cursor: not-allowed;
    pointer-events: none;
}

.alert{
    font-size: 1.2rem;
}

.alert > h5{
    background-color: rgb(252, 97, 97);
    color:white;
    border: 2px solid red;
    padding:10px 15px;
    border-radius: 5px;
}

.transaction{
    max-width: 700px;
    margin: 50px auto;
}

.transaction > h1{
    margin: 10px;
    background-color: #004c8c;
    border: 2px solid #004c8c;
    color: white;
    font-size: 24px;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
}


.transaction > h1:hover{
    color: #004c8c;
    background-color: lightblue;
}


form{
    padding: 10px ;
}

.transaction form label{
    font-size: 20px;
    margin: 15px 0;
    color: #004c8c;
    font-weight: bolder;
}

.transaction> form> input{
    display: block;
    margin: 5px 0;
    margin-bottom: 25px;
    width: 100%;
    padding: 5px 10px;
    font-size: 24px;
    border-radius: 5px;
    background-color: #d0fcfc;
    border : 2px solid #0277bd;
}


button{
    font-size: 20px;
    padding: 8px 15px;
    border-radius: 5px;
    color: #0277bd;
    border: 2px solid #0277bd;
}

button:hover{
    background-color: #0277bd;
    color: white;
}

input[type="radio"]{
    display: inline;
}

label{
    display: block;
}

.balance{
    text-align: center;
    font-size: 1.5rem;
    padding: 10px 0;
    margin-bottom: 20px;
    border-bottom: 2px solid grey;
}

.great{
    color: rgb(8, 88, 8);
}

.danger{
    color: red;
}


.amountBox{
    margin: 0 100px; 
}


.list{
    margin: 10px 0;
    display: flex;
    justify-content: space-evenly;
    background-color: #eeeeee;
    border-radius: 10px;
}

.list p{
    margin: 10px 0;
    font-size: 1.5rem;
    width: 200px;
    text-align: center;
}

.green {
    border: 2px solid green;
    background-color: rgb(163, 255, 163);
    /* box-shadow: 2px 2px 4px #09b917, -2px -2px 4px #09b917; */
}

.red{
    border: 2px solid red;
    background-color: rgb(248, 167, 167);
    /* box-shadow: 2px 2px 4px #ee0808, -2px -2px 4px #ee0808; */
}

.red-cross{
    color: #fc5f5f;
    font-weight: 700;
    cursor: pointer;
}

.red-cross:hover{
    color : red;
    transform: scale(1.2);
}
